	import uniDrawer from "@/components/uni-ui/uni-drawer/uni-drawer.vue"
	import card from "@/components/common/card.vue"
	import scradioGroup from "@/components/common/scradio-group.vue"
	
	import searchList from "@/components/search-list/search-list.vue"
	export default {
	    components: {uniDrawer,card,scradioGroup,searchList},
		data() {
			return {
				screen:{
					currentIndex:0,
					list:[
						{name:'综合',status:1},
						{name:'销量',status:0},
						{name:'价格',status:0},
						
					]
				},
				label:{
					selected:0,
					list:[
						{name:'one'},
						{name:'two'},
						{name:'three'}
					]
				},
				list:[
					{
						title:'白菜',
						cover:"/static/del/1.jpg",
						desc:"好好都i为佛教哦i文件发客人分回复额我发你",
						price:"1.5",
						paynum:"23",
					}
				]
				
			}
		},
		methods: {
			changeScreen(index){
				//判断当前是否是激活状态
				let oldIndex = this.screen.currentIndex
				let oldItem = this.screen.list[oldIndex]
				//处于激活状态
				if(oldIndex===index){
					return oldItem.status = oldItem.status===1?2:1
				}
				let newItem = this.screen.list[index]
				//移除已激活状态
				oldItem.status=0
				this.screen.currentIndex=index
				//增加激活状态
				newItem.status=1
		},
		 openDrawer(){
			 this.$refs.drawer.open()
		 },
		 changeRadio(index){
			 this.label.selected = index
		 }
	}
}
